.progress-container {
  width: 80%;
  margin: auto;
}

.progress {
  background-color: #e0e0e0;
  border-radius: 10px;
  overflow: hidden;
  margin-bottom: 15px;
}

.progress-bar {
  height: 15px;
  width: 0;
  background-color: #3b82f6;
  text-align: center;
  color: white;
  line-height: 15px;
  font-size: 12px;
  font-weight: bold;
  transition: width 0.3s ease;
}

/* Container to position the circular progress */
.progress-circle {
  width: 150px;
  height: 150px;
  position: relative;
}

/* Circle background */
.circle-bg {
  width: 70%;
  height: 70%;
  border-radius: 50%;
  background: #e0e0e0;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

/* Circle progress */
.circle-progress {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  clip: rect(0px, 75px, 150px, 0px);
  background: #3b82f6;
  position: absolute;
  transform: rotate(0deg);
  animation: progress 2s linear forwards;
}

/* Left side of the progress bar */
.circle-progress::before {
  content: "";
  width: 100%;
  height: 100%;
  border-radius: 50%;
  clip: rect(0px, 150px, 150px, 75px);
  background: #3b82f6;
  position: absolute;
  transform: rotate(180deg);
}

/* Inner text (percentage) */
.inner-circle {
  width: 50%;
  height: 50%;
  border-radius: 50%;
  background: white;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 18px;
  font-weight: bold;
  color: #3b82f6;
  position: relative;
  z-index: 1;
}

/* Keyframes for rotation animation */
@keyframes progress {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(135deg);
  } /* Adjust for percentage */
}

@media screen and (max-width: 450px) {
  .w-20 {
    width: 5rem;
    width: 100%;
  }
  ul.menus {
    display: flex;
    gap: 10px;
    justify-content: space-around;
    padding: 0;
    margin: 20px 20px 0 20px;
  }
  .menus p {
    font-size: 18px;
  }
  .menus .text-2xl {
    font-size: 30px;
  }
  .menus .border-t-2 {
    border-top-width: 0px;
  }
  .menu-container {
    height: 135px;
    position: fixed;
    background: #312e2e85;
  }

  .hero {
    margin: 0 10px;
    width: auto;
    margin-top: 140px;
  }
  .inner-panel {
    width: auto;
    margin: 0 10px;
  }
  .h-\[320px\] {
    height: 220px;
  }
}

@media screen and (min-width: 701px) and (max-width: 780px) {
  .w-20 {
    width: 5rem;
    width: 100%;
  }
  ul.menus {
    display: flex;
    gap: 10px;
    justify-content: space-around;
    padding: 0;
    margin: 20px 20px 0 20px;
  }
  .menus p {
    font-size: 18px;
  }
  .menus .text-2xl {
    font-size: 30px;
  }
  .menus .border-t-2 {
    border-top-width: 0px;
  }
  .menu-container {
    height: 140px;
    position: fixed;
    background: #312e2e85;
    top: 0;
    left: 0;
  }
  .hero {
    margin-top: 120px;
  }
  .inner-panel {
    width: auto;
  }
}
